// src/styles/ChatButton.scss
$button-size: 64px; // Size of the button
$button-color: transparent; // Make the button background transparent
$button-pressed-color: black; // Color when pressed

.chat-button {
  position: fixed;
  bottom: 16px;
  right: 16px;
  width: $button-size;
  height: $button-size;
  background-color: $button-color; // Transparent background
  color: white;
  border: none; // Remove border
  border-radius: 50%;
  box-shadow: none; // Remove shadow
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s;
  cursor: pointer; // Change cursor to pointer on hover

  &.pressed { // Add a class for pressed state
    background-color: $button-pressed-color; // Black when pressed
  }

  img {
    width: 60px; // Size of the human face image
    height: 60px; // Size of the human face image
    border-radius: 50%; // Optional: make the image circular
  }

  svg {
    width: 24px; // Size of the dropdown icon
    height: 24px; // Size of the dropdown icon
    color: white; // Color of the dropdown icon
  }
}